import React, {useEffect, useRef} from 'react';
import styled from 'styled-components';
import Icon from "./Icon";

const Container = styled.div`
    width: 100%;
    height: 350px;
    overflow: auto;
    display: flex;
    flex-wrap: no-wrap;
`;

const Arrow = styled.div`
    transition: opacity 0.3s ease;
    position: absolute;
    color: white;
    text-shadow: 0 0 8px black;
`;

const Carousel = props => {
    const body = useRef(null);
    const arrowLeft = useRef(null);
    const arrowRight = useRef(null);

    let offset, index = 0, currentScroll = 0;
    let timer, animationFrame;

    const handleScroll = () => {
        if(index === 0) {
            arrowLeft.current.style.opacity = 0;
        } else {
            arrowLeft.current.style.opacity= 1;
        }

        if(index === body.current.childElementCount - 3) {
            arrowRight.current.style.opacity = 0;
        } else {
            arrowRight.current.style.opacity = 1;
        }

        window.clearTimeout(timer);
        if(currentScroll === body.current.scrollLeft) return;
        window.clearTimeout(animationFrame);

        offset = body.current.scrollLeft;
        index = Math.floor((body.current.offsetWidth / 2 + body.current.scrollLeft) / body.current.offsetWidth);

        timer = window.setTimeout(checkScroll, 150);
    };

    const checkScroll = () => {
        animate(0, 30);
    };

    const animate = (i, count) => {
        if(count + 1 === i) return;
        animationFrame = window.setTimeout(() => {
            currentScroll = Math.round((body.current.offsetWidth * index - offset) * (i / count) * (i / count) * (i / count) + offset);
            body.current.scrollLeft = currentScroll;
            animate(i + 1, count);
        }, 100);
    };

    useEffect(() => {
        window.addEventListener('resize', positionArrows);
        positionArrows();

        return () => {
            window.removeEventListener('resize', positionArrows);
        }
    });

    const positionArrows = () => {
        arrowLeft.current.style.top = body.current.offsetTop + 100 + 'px';
        arrowLeft.current.style.left = '12px';

        arrowRight.current.style.top = body.current.offsetTop + 100 + 'px';
        arrowRight.current.style.left = document.documentElement.offsetWidth - 60 + 'px';
    };

    return (
        <Container ref={body} onClick={() => console.log('hey')} onScroll={handleScroll}>
            <Arrow ref={arrowLeft}>
                <Icon color={'rgba(255, 255, 255, 0.9)'} size={'48px'}>arrow_back</Icon>
            </Arrow>
            <Arrow ref={arrowRight}>
                <Icon color={'rgba(255, 255, 255, 0.9)'} size={'48px'}>arrow_forward</Icon>
            </Arrow>
            {props.children}
        </Container>
    );
};

export default Carousel;
