import React from 'react';
import styled from 'styled-components';
import Icon from "./Icon";

const Container = styled.div`
    flex-grow: 1;
    padding: 0 16px;
    color: #666;
`;

const Input = styled.input`
    border: none;
`;

const Search = props => {
    return (
        <Container>
            <Input placeholder={'in Xise suchen'} />
        </Container>
    )
};

export default Search;
